import React, { Component } from 'react';

import {
  Link,
} from 'react-router-dom';

import {post} from '../config/fetch';

import Header from '../components/header';
import Button from '../components/button';
import Loading from '../components/loading';

import './info.css';

/**/
import line1 from '../images/line1.png';
import line2 from '../images/line2.png';
import line3 from '../images/line3.png';
import no1 from '../images/no1.png';
import no2 from '../images/no2.png';
import no3 from '../images/no3.png';
/* */
import Bg from '../images/info2.jpg';
const infoBgStyle = {
    backgroundImage: 'url('+Bg+')'
}
class Info extends Component {
    constructor(props){
        super(props);
        this.state={
            isOver :false,
            endTime : '',
            userParams:[]
        }
    }
    componentWillMount(){
        let param = new URLSearchParams(this.props.location.search).get('user_id');
        if(param === null){
            this.props.history.replace({pathname:'/alert',search:this.props.location.search,state: { modal: true,content:`APP`,updateUrl:1 }})
        }else{
            post('/getgameconfig',{user_id:param}).then(
                (Response)=>{
                    this.setState({
                        endTime:Response.game_end_time,
                        userParams:Response
                    });
                    let endTimeStramp = new Date(Response.game_end_time).getTime(),
                        nowTimeStramp = new Date().getTime();
                    // nowTimeStramp > endTimeStramp && this.setState({isOver:true}); //
                    nowTimeStramp > endTimeStramp && this.setState({isOver:false}); //alert
                    // console.log(this.state.isOver)
                }
            )
        }
    }
    renderMainContent(){
        let searchUrl = this.props.location.search;
        return(
        <div>
            <div className="common-pos html-wrap" style={infoBgStyle}>
                <Header title=""/>
                <div className="content-wrap">
                    <div className="content">
                        <div className="card">
                            <ul>
                                <li><img src={line1} alt=""/>80320:00-81324:00</li>
                                <li><img src={line2} alt=""/><span className="special-color">100</span></li>
                                <li><img src={line3} alt=""/>3</li>
                                <div className="content-tip"><span className="special-color">+1</span></div>
                            </ul>
                            <div className="content-copyright"></div>
                        </div>
                        <div className="card card-add">
                            <div className="card-title"></div>
                            <ul>
                                <li className="special-li">
                                    <img src={no1} alt=""/>
                                    <div className="special-li-div">
                                        <div>&nbsp;4+3<br/></div>
                                        <div>()&nbsp;<span className="special-color">8888</span></div>
                                    </div>
                                </li>
                                <li><img src={no2} alt=""/>3000<span style={{color: '#7c9fbd'}}>&nbsp;&nbsp;<span className="special-color">2400</span></span></li>
                                <li><img src={no3} alt=""/>1000</li>
                                <li>
                                    <div className="left">4-10</div>
                                    <div className="right">ysl<span style={{color: '#7c9fbd'}}>&nbsp;200</span></div>
                                </li>
                                <li>
                                    <div className="left">11-50</div>
                                    <div className="right">20</div>
                                </li>
                                <li>
                                    <div className="left">51-100</div>
                                    <div className="right">10</div>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <Link to={this.state.isOver ? {pathname:'/index',search:searchUrl,state:{isOver:1,userParams:this.state.userParams}} : {pathname:'/index',search:searchUrl,state:{isOver:0,userParams:this.state.userParams}}} 
                          className="router-common"
                    >
                        <Button  wrapColor={{width:'60%'}} padColor={{border: '1px solid #fddb3d'}}  name=""/>
                    </Link>

                    <div className="btn-flex">
                        <Link to={this.state.isOver ? {pathname:'/list',search:searchUrl,state: { modal: false }} : {pathname:'/alert',search:searchUrl,state: { modal: true,content:`${this.state.endTime}` }}} 
                              className="router-common" style={{width:'40%'}}
                        >
                            <Button
                                padColor={{background: 'linear-gradient(#fa94a9, #eb4863)'}}
                                innerColor={{background: '#f65c82'}}
                                name=""
                            />
                        </Link>
                        <Link to={this.state.isOver ? {pathname:'/score/per',search:searchUrl,state:{isOver:1}} : {pathname:'/score/all',search:searchUrl,state:{isOver:0}}} 
                              className="router-common" style={{width:'40%'}}
                        >   
                            <Button padColor={{background: 'linear-gradient(#79e0cf, #49b675)'}} innerColor={{background: '#2eceb4'}} name="" path='/score'/>
                        </Link>
                    </div>
                </div>
            </div>
        </div>
        )
    }
    renderLoadingGif(){
        return(
            <Loading />
        )
    }
  render() {
    return (
        // this.state.isMount ? this.renderMainContent() : this.renderLoadingGif()
        this.renderMainContent()
    );
  }
}

export default Info;



